@media (min-width: 80em) {
  .banner {
    margin: -3em 0; } }

@media (min-width: 64em) {
  .banner__content {
    padding: 1em 5.5%; } }

@media (min-width: 64em) {
  .banner__caption {
    background: transparent;
    visibility: hidden;
    display: none;
    position: absolute;
    left: 0;
    top: 30%;
    right: 0;
    padding: 1em 5.5%; } }

@media (min-width: 64em) {
  .banner__title {
    font-size: 1.778em; } }

@media (min-width: 64em) {
  .banner__subtitle {
    font-size: 1em; } }

@media (min-width: 64em) {
  .banner--main .banner__title,
  .banner--main .banner__subtitle {
    font-size: 2.813em; } }

@media (min-width: 64em) {
  .banner--main .banner__caption {
    display: block;
    float: right;
    top: 4em; } }

@media (min-width: 80em) {
  .banner--main .banner__caption {
    left: 45%; } }

@media (min-width: 45em) {
  .burger-nav__icon {
    width: 35px; } }

@media (min-width: 64em) {
  .burger-nav__icon {
    width: 42px; } }

@media (min-width: 80em) {
  .languageSearch-wrapper {
    z-index: -9999999999; } }

@media (min-width: 45em) {
  .languages--footer {
    padding: 0 1em 0 0; } }

@media (min-width: 64em) {
  .languages--footer {
    padding: 0 5% 0 0; } }

@media (min-width: 64em) {
  .languages-wrapper {
    position: absolute;
    top: 4em;
    left: 0;
    right: 0;
    height: auto;
    width: 100%; } }

@media (min-width: 80em) {
  .languages-list__item {
    padding: 0.35em 5px 0; } }

.languages-list__label {
  color: #000; }

@media (min-width: 45em) {
  .languages-list__title {
    padding: 0 0 0 0.313em; } }

@media (min-width: 45em) {
  .languages-list__anchor {
    font-size: 0.889em; } }

@media (min-width: 64em) {
  .nav-wrapper {
    position: absolute;
    top: 4em;
    left: 0;
    right: 0;
    height: auto;
    width: 100%; } }

@media (min-width: 64em) {
  .search-wrapper {
    position: absolute;
    top: 4em;
    left: 0;
    right: 0;
    height: auto;
    width: 100%; } }
